.app{
    background: url("https://images.unsplash.com/photo-1611920132872-0ccdcd9e54ee?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1050&q=80") #332839fb;
    background-blend-mode: multiply;
    background-repeat: no-repeat;
    background-size: cover;
    height:100vh;
    width:auto;
    padding: 10px 20px;
}

.home{
    
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    top:20%;
}

.home-text>h1{
    color: white;
    font-family: optima;
    font-weight: bold;
    font-size: 80px;
    color: #fff;
    text-align: center;
   
}

.home-text-2{
    
    letter-spacing: 15px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.home-btn>button {
    font-size: 20px;
    text-align: center;
    border:none;
    outline-width: 0;
    padding: 10px 20px;
    background-color: transparent;
    border:2px solid white;
    color: #fff;
    
    
  }

  .home-btn>button:hover{
    text-shadow: 0 0 20px #fff, 0 0 30px #ff4da6, 0 0 40px #ff4da6, 0 0 50px #ff4da6, 0 0 60px #ff4da6, 0 0 70px #ff4da6, 0 0 80px #ff4da6;
  }